.main {
  background-color: #fff;
  border-radius: 15px;
}

//---------- Slider - Swiper :
.swiper {
  max-width: 100%;
  margin-bottom: 20px;
}

.swiper-wrapper {
  padding: 20px 0;
}

.wrapper__swiper {
  position: relative;
}

.swiper-slide {

  img {
    width: 100%;
    border-radius: 20px;
  }
}

.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-horizontal>.swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 25px;
}

.swiper-button-prev,
.swiper-button-next {
  width: calc(var(--swiper-navigation-size) / 44 * 45);
  color: rgb(61, 61, 61);
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.705);
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  color: purple;
  background-color: rgba(255, 177, 242, 0.164);
}

.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 28px;
}

.swiper-pagination-bullet-active {
  background-color: rgb(149, 0, 168);
}

//------- Carts :
.main__title {
  display: inline-block;
  margin-bottom: 15px;
  padding-left: 50px;
}

.main__cards {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin: 0 -10px;
  background-color: rgb(243, 243, 243);
  border-radius: 1rem;

}

.main__item {
  width: 20%;
  padding: 5px;
  border-radius: 10px;
}

.main__product {
  padding: 5px;
  border-radius: 15px;
  // background-color: rgba(208, 232, 255, 0.548);

}

.main__product-image {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
}

.product-image__present {
  height: 250px;

  img {
    width: 100%;
    height: 100%;
  }
}

.product-image__modal {
  opacity: 0;
  position: absolute;
  width: 75%;
  top: 50%;
  left: 50%;
  padding: 0.2rem;
  border: 1px solid rgb(172, 0, 252);
  border-radius: 6px;
  transform: translate(-50%, -50%);
  cursor: pointer;
  transition: 0.2s;
}

.main__product-image:hover {

  .product-image__modal {
    opacity: 1;
    cursor: pointer;
  }
}

.product-image__modal:active {
  background-color: rgb(204, 202, 202);
}

.product-image__sale {
  position: absolute;
  left: 3%;
  bottom: 3%;
  padding: 5px;
  color: purple;
  font-size: 12px;
  font-weight: 700;
  border: 1px solid rgb(172, 0, 252);
  border-radius: 50%;
  background-color: #fff;
}

.product-image__add-cart {
  background-color: rgb(172, 0, 252);
  border-radius: 50%;
  width: 30px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  position: absolute;
  bottom: 3%;
  right: 3%;
  border: 1px solid rgb(172, 0, 252);
  border: none;
  transition: 0.2s;
}

.product-image__add-cart:active {
  background-color: rgb(125, 2, 182);
}

.main__product-info {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.product-info__item {
  width: 50%;
  padding-top: 10px;
}

.product-info__price {
  text-decoration: line-through;
  width: 50%;
}