@import 'variables';

.header__body {
  @include diflex;
  padding: 10px 0;

}

.header__burger-outline {
  padding: 10px 8px;
  border: 1px solid rgb(214, 213, 213);
  border-radius: 7px;
}

.header__burger-outline:hover {
  border-color: #fff;
}

.header__burger {
  display: block;
  position: relative;
  width: 30px;
  height: 24px;
  background: transparent;
  border: none;
  outline: none;
}

.header__burger-line {
  width: 100%;
  height: 4px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #fff;
  border-radius: 10px;
  transition: opacity 0.3s ease-in-out;
}

.header__burger::before {
  @include fakeburgerline;
  top: 0;
}

.header__burger::after {
  @include fakeburgerline;
  bottom: 0;
}

.header__search-form {
  width: 50%;
  padding: $search-form-padding;
  background-color: $search-form-background-color;
  border: none;
  border-radius: $search-form-border-radius;
  outline: none;
  transition: 0.4s;
  color: #fff;
  font-size: 16px;
}

.header__search-form:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.89);
  color: black;
}

.header__search-form::placeholder {
  color: rgb(180, 180, 180);
}

.header__search-form:hover::placeholder {
  color: black;
}

.header__search-form::before {
  content: "";
  top: 50px;
  left: -50px;
  width: 20px;
  height: 20px;
}

.header__nav-bar {
  @include diflex;

  .btn-cart {
    display: flex;
    flex-flow: column;
    align-items: center;
    background-color: inherit;
    border: 1px solid rgb(202, 200, 200);
    border-radius: 15 px;

    .cart-svg {
      max-width: 35px;
      max-height: 35px;
    }
  }

  .btn-cart:hover {
    background-color: inherit;
    border: 1px solid #fff;
  }
}

.modal-header {
  color: #fff;
  font-size: 24px;
  background-color: rgb(111, 0, 175);

}

.modal-footer {
  background-color: rgb(196, 36, 142);
  display: flex;
  justify-content: space-between;

  .btn-cart-clear {
    border: none;
    border-radius: 0.5rem;
    padding: 0.3rem;
    font-size: 1rem;
  }
}

.cart-list__empty {
  display: block;
  font-size: 24px;
}

.cart-list__empty--none {
  display: none;
}

.cart-item {
  @include diflex;
  padding: 20px 10px;
  border-bottom: 1px solid rgb(179, 0, 250);
  font-size: 20px;
}