//---Mixin---
@mixin diflex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin fakeburgerline {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background: #fff;
  border-radius: 10px;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out,
    bottom 0.3s ease-in-out;
}

//---Variables---
$margin-auto: 0 auto;
$wrapper-max-width: 1200px;
$wrapper-padding: 0 15px;
$container-background: linear-gradient(90deg,
    rgb(196, 36, 142),
    rgb(111, 0, 175));
$search-form-padding: 15px 30px;
$search-form-background-color: rgba(193, 36, 255, 0.253);
$search-form-border-radius: 15px;